export const login = (isloged) => {
    return (dispatch) => {
        dispatch({
            type: "login",
            payload: isloged,
        });
    };
};

export const logout = (isloged) => {
    console.log(isloged, "Lol");
    return (dispatch) => {
        dispatch({
            type: "logout",
            payload: isloged,
        });
    };
};