/* eslint-disable no-unused-vars */
import "./App.css";
import { useContext, useEffect, lazy, Suspense } from "react";
import Cookies from "js-cookie";
import { stateContext } from "./StateContext";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";
import { Loading } from "./components";
/*Redux*/
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { bindActionCreators } from "redux";
import { actionCreators } from "./state/index";
/***** */
const Dashboard = lazy(() => import("./pages/jsx/Dashboard"));
const Authentication = lazy(() => import("./pages/jsx/Authentication"));
const PayemntPage = lazy(() => import("./pages/jsx/PayemntPage"));
const Footer = lazy(() => import("./pages/jsx/Footer"));
const Team = lazy(() => import("./pages/jsx/Team"));
const Console = lazy(() => import("./pages/jsx/Console"));
const History = lazy(() => import("./pages/jsx/History"));
const ResetPassword = lazy(() => import("./pages/jsx/ResetPassword"));
const Account = lazy(() => import("./pages/Account/jsx/Account"));
const Home = lazy(() => import("./pages/Home/Home"));

function App() {
  const state = useSelector((state) => state.User);

  const dispatch = useDispatch();

  const { login, logout } = bindActionCreators(actionCreators, dispatch);

  console.log(state);

  let [Logedin, setLogin] = useContext(stateContext);
  const ReadCoockie = () => {
    const user = Cookies.get("user");
    if (user) {
      login(true);
      setLogin(true);
    }
  };
  useEffect(() => {
    ReadCoockie();
  });
  return (
    <div className="App">
      <Routes />
    </div>
  );
}

const PertectedLogin = ({ auth, component: Component, ...rest }) => {
  return (
    <Route
      {...rest}
      render={() =>
        !auth ? (
          <>
            <Component />
          </>
        ) : (
          <Redirect to="/" />
        )
      }
    />
  );
};

const Routes = (props) => {
  let [Logedin] = useContext(stateContext);
  const state = useSelector((state) => state.User);

  console.log(state);
  return (
    <Router basename="/PlatformX">
      <Suspense fallback={<Loading />}>
        {" "}
        <Switch>
          <PertectedLogin
            path="/login"
            auth={state}
            component={() => <Authentication />}
          />{" "}
          <Route path={`/Config_account`} component={() => <ResetPassword />} />
          <Route
            path={"/Console"}
            render={({ match: { path } }) => (
              <Console auth={state}>
                <Switch>
                  <Route
                    exact
                    path={path + "/"}
                    component={() => <> page1 </>}
                  />
                  <Route path={`${path}/tab1`} component={() => <> page2 </>} />
                  <Route path={`${path}/tab2`} component={() => <> page2 </>} />
                  <Route path={`${path}/tab3`} component={() => <> page3 </>} />
                  <Route path={`${path}/tab4`} component={() => <> page4 </>} />
                  <Redirect exact from={path + "/*"} to={path} />{" "}
                </Switch>{" "}
              </Console>
            )}
          />{" "}
          <Route
            path={"/"}
            render={({ match: { path } }) => (
              <Home auth={state}>
                <Switch>
                  <Route exact path={"/"} component={() => <Dashboard />} />{" "}
                  <Route path={`${path}team`} component={() => <Team />} />{" "}
                  <Route
                    path={`${path}Histroy`}
                    component={() => <History />}
                  />{" "}
                  <Route
                    path={`${path}Pricing`}
                    component={() => <PayemntPage />}
                  />{" "}
                  <Route path={`${path}about`} component={() => <Footer />} />{" "}
                  <Route
                    path={`${path}Profile`}
                    render={({ match: { path } }) => (
                      <Account>
                        <Switch>
                          <Route
                            exact
                            path={path + "/"}
                            component={() => <> page1 </>}
                          />
                          <Route
                            path={`${path}/Edite`}
                            component={() => <> page2 </>}
                          />
                          <Redirect exact from={path + "/*"} to={path} />{" "}
                        </Switch>{" "}
                      </Account>
                    )}
                  />{" "}
                  {/* <Redirect exact from={path + "*"} to={path} />{" "} */}{" "}
                </Switch>{" "}
              </Home>
            )}
          />{" "}
          <Route path="*" component={() => <div> 404 Not found </div>} />
        </Switch>{" "}
      </Suspense>{" "}
    </Router>
  );
};

export default App;
